import { useEffect, useLayoutEffect } from "react";
import ReactDOM, { createPortal } from "react-dom";
import { StyledCross, StyledNotification } from "./styled";

function Notification({ label, backgroundColor }) {
  const modalRoot = document.getElementById("custom-root");
  let elem = document.createElement("div");
  useLayoutEffect(() => {
    modalRoot.appendChild(elem);
    return () => {
      modalRoot.removeChild(elem);
    };
  }, []);

  return createPortal(
    <StyledNotification backgroundColor={backgroundColor}>
      {label}
      <StyledCross>X</StyledCross>
    </StyledNotification>,
    elem,
  );
}

export default Notification;
